code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Navigation {
    background-color: ghostwhite;
    position: static;
    border-radius: 20px;
    margin: 40px 200px 0 200px;
    display: flex;
}

.Navigation .logo {
    text-decoration: none;
    width: 100%;
    margin-left: 50px;
    display: flex;
    cursor: pointer;
}

.Navigation .logo img {
    margin: 15px 2px 15px 0;
    width: 65px;
}

.Navigation .logo p {
    font-family: 'Potta One', cursive;
    color: #017fbe;
    margin: 0 auto;
    margin-left: 5px;
    font-size: 65px;
}

.Navigation ul {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    padding-right: 80px;
    margin: auto;
}

.Navigation .links {
    width: 70px;
    height: 20px;
    text-decoration: none;
    list-style-type: none;
    font-size: 16px;
    font-weight: bold;
    margin: 0 auto;
    padding: 10px 15px 15px 15px;
    cursor: pointer;
    border-radius: 10px;
    background-color: #017fbe;
    color: ghostwhite;
}


.Home {
    text-align: center;
}

.Home h1 {
    background-color: ghostwhite;
    border-radius: 15px;
    width: 40%;
    margin: 0 auto;
    margin-top: 30px;
    color: #017fbe;

}

.Events {
    max-width: 100%;
    display: flex;
    flex-wrap: wrap;
    margin: 40px 250px 0 250px;
}

.Events .Event {
    margin: 0 auto;
    margin-bottom: 30px;
}
.Event {
    width: 30%;
    background-color: ghostwhite;
    border-radius: 15px;
    display: block;
}

.Event .description {
    height: 50px;
    padding: 20px;
}

.Event img {
    height: 180px;
    -webkit-filter: brightness(60%);
            filter: brightness(60%);
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
    width: 100%;
}

.Event .likes, .buttons {
    position: relative;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-bottom: 20px;
}

.Event .likes .blue {
    color: #00b7ff;
}

.Event .likes i {
    width: 20px;
    height: 20px;
    background-color: ghostwhite;
    position: absolute;
    left: 20px;
    color: black;
}

.Event .likes span {
    position: absolute;
    bottom: 5px;
    left: 50px;
}

.Event .buttons .links {
    width: 50px;
    position: absolute;
    text-decoration: none;
    padding: 5px;
    border: 1px solid #282c34;
    border-radius: 3px;
}

.Event .links:nth-child(1) {
    right: 100px;
    background-color: #017fbe;
    color: ghostwhite;
}

.Event .links:nth-child(2) {
    right: 20px;
    background-color: crimson;
    color: ghostwhite;
}

.Event .details:hover {
    -webkit-filter: brightness(40%);
            filter: brightness(40%);
    cursor: pointer;
}
.Register, .Login, .Create, .Edit, .Details {
    background-color: ghostwhite;
    border-radius: 5px;
    width: 20%;
    margin: 0 auto;
    margin-top: 30px;
}

.title {
    padding-top: 20px;
    font-size: 20px;
    font-weight: 500;
}

.input {
    margin: 25px 0;
}

.input input {
    width: 80%;
    height: 30px;
    border: 2px solid gainsboro;
    border-radius: 5px;
    padding: 6px 0 5px 10px;
}

.input input::-webkit-input-placeholder {
    color: lightslategrey;
}

.input input:-ms-input-placeholder {
    color: lightslategrey;
}

.input input::placeholder {
    color: lightslategrey;
}

button {
    width: 80%;
    height: 30px;
    margin: auto;
    margin-bottom: 20px;
    background-color: #017fbe;
    border: transparent;
    border-radius: 5px;
    color: ghostwhite;
    font-size: 14px;
}


.Profile {
    display: flex;
    width: 80%;
    margin: 30px auto;
}

.Profile .likedEvents, .createdEvents {
    max-width: 40%;
    margin: 0 auto;
}

.Profile h1 {
    background-color: ghostwhite;
    border-radius: 15px;
    width: 350px;
    margin: 0 auto;
    margin-bottom: 30px;
    color: #017fbe;
}

.Profile .Event {
    width: 70%;
    height: inherit;
    margin: 0 auto;
    margin-bottom: 30px;
}

.Profile .Event .buttons {
    padding-bottom: 40px;
}

.Profile .profile-data {
    width: 30%;
    height: 30%;
    margin: 0 auto;
    background-color: ghostwhite;
    border-radius: 5px;
    padding: 20px 15px 0 15px;
}

.Profile .profile-data img {
    width: 100px;
}

.Profile .profile-data .name {
    font-weight: bold;
}

.Profile .profile-data .username {
    color: #017fbe;
}

.Profile .profile-data .buttons {
    display: flex;
}

.Profile .profile-data .buttons button {
    height: 40px;
    margin: 0 10px;
}


.Details {
    background-color: ghostwhite;
    border-radius: 5px;
    width: 40%;
    margin: 0 auto;
    margin-top: 30px;
    margin-bottom: 30px;
}

.Details img {
    border-top-right-radius: inherit;
    border-top-left-radius: inherit;
    width: 100%;
}

.Details .question-answers {
    padding: 20px;
}

.Details .question {
    font-style: italic;
}

.Details p {
    font-weight: bold;
}

.Details .question:nth-child(1) {
    margin-top: 100px;
}

.Details .likes .blue {
    color: #00b7ff;
}

.Details .likes i {
    width: 20px;
    height: 20px;
    background-color: ghostwhite;
    color: black;
}
